export default {
  data() {
    return {
      tableData: [],
      title: "新增任务",
      visible: false,
      //任务配置数据
      form: {
        task_type: "",
        task_describe: "",
        task_target: "",
        fertilizers: "",
        is_draw: false,
        task_guide: ""
      },
      rules: {
        task_type: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        task_describe: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        task_target: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        fertilizers: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        is_draw: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        task_guide: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      optiontype: [
        {
          value: 1,
          label: "登录游戏大厅"
        },
        {
          value: 2,
          label: "分享微信朋友"
        },
        {
          value: 3,
          label: "分享微信朋友圈"
        },
        {
          value: 4,
          label: "游戏流水达到特定值"
        },
        {
          value: 5,
          label: "发展下级数量达到特定值"
        }
      ],
      optionguide: [
        {
          value: 1,
          label: "无指引"
        },
        {
          value: 2,
          label: "跳转分享微信朋友圈"
        },
        {
          value: 3,
          label: "跳转分享微信朋友"
        }
      ],
      keys: "",
      id: "",
      //活动所有数据
      allData: {},
      //10003 数据
      currentData: {},
      //保存当前编辑的下标
      currentIndex: "",
      loading: false
    };
  },
  created() {
    this.initData();
  },
  methods: {
    add() {
      this.editForm(true, "新增", this.form);
    },

    //发送服务器配置
    async send() {
      this.loading = true;
      let postData = {};
      this.tableData.forEach((item, index) => {
        postData[index + 1] = item;
      });
      this.currentData.ac_content.task = postData;
      // console.log(this.currentData,this.allData);
      let { data } = await this.$http.HallFunConfig.PostActivityNew3({
        keys: this.keys,
        values: JSON.stringify(this.allData),
        id: this.id
      });
      if (data.code === 1 && data.msg === "ok") {
        this.loading = false;
        this.$message({
          type: "success",
          message: "发送服务器配置成功!"
        });
      } else {
        this.loading = false;
        this.$message({
          type: "warning",
          message: "发送服务器配置失败!"
        });
      }
    },

    handleEdit(index, row) {
      this.currentIndex = index;
      this.editForm(true, "编辑", { ...row });
    },

    //表格删除
    handleDelete(index, row) {
      this.tableData = this.tableData.filter((item, idx) => {
        return index !== idx;
      });
    },

    //新增和编辑的提交
    onSubmit(formName, title) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (title === "新增") {
            this.tableData.push(this.form);
          } else if (title === "编辑") {
            let resTable = [];
            this.tableData.forEach((item, index) => {
              resTable.push(item);
              if (this.currentIndex === index) {
                resTable[index] = this.form;
              }
            });
            this.tableData = resTable;
            // console.log(this.tableData,resTable);
          }
          this.editForm(false, "新增", this.form);
        } else {
          this.$message({
            type: "warning",
            message: "必填的项不可以为空!"
          });
          return false;
        }
      });
    },
    back() {},

    editForm(visible, title, form) {
      this.visible = visible;
      this.title = title;
      this.form = form;
    },

    async initData() {
      let { data } = await this.$http.HallFunConfig.GetActivityNew3({
        key: "activity_new.lua"
      });
      //   console.log(data);
      this.keys = data.data[0].sys_key;
      this.id = data.data[0].id;
      let res = data.data[0].sys_val;
      this.allData = JSON.parse(res);
      Object.keys(this.allData).forEach(item => {
        if (this.allData[item].ac_type === "10003") {
          this.currentData = this.allData[item];
          this.tableData = Object.values(this.allData[item].ac_content.task);
          this.total = this.tableData.length;
        }
      });

      // console.log(this.tableData, this.allData);
    }
  }
};



